import React, { useEffect, useState } from "react";
import "./cart.css";

import { Col, Container, Row } from "reactstrap";
import tdImg from "../../assets/images/arm-chair-01.jpg";

import { cartActives } from "../../redux/slices/cartSlice";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import Helmet from "../../assets/helmet/Helmet";

function Cart(props) {
  const [paymentUrl, setPaymentUrl] = useState("");

  useEffect(() => {
    window.scrollTo(0, 0);
  }, []);
  const totalQuantity = useSelector((state) => state.cart.totalQuantity);

  const totalAmounts = useSelector((state) => state.cart.totalAmount);

  const cartItems = useSelector((state) => state.cart.cartItem);

  const [totalAmount, setTotalAmount] = useState(totalAmounts);

  const getRandomPercentage = () => {
    return Math.floor(Math.random() * 41) + 10; // To s ngu nhin t 10 n 50
  };
  function calculateOriginalPrice(salePrice, discountPercentage) {
    // Chuyn t l gim gi t phn trm thnh s thp phn
    const discountDecimal = discountPercentage / 100;

    // Tnh gi gc trc khi sale
    const originalPrice = salePrice / (1 - discountDecimal);

    // Lm trn gi gc v 2 ch s thp phn (nu cn)
    const roundedOriginalPrice = Math.round(originalPrice * 1) / 1;

    return roundedOriginalPrice;
  }
  const formatNumberWithCommas = (number) => {
    return number.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
  };

  // const formattedPrice = formatNumberWithCommas(props.item.price);

  const randomPercentage = getRandomPercentage();

  useEffect(() => {
    window.scrollTo(0, 0);

    localStorage.setItem("cart", JSON.stringify(cartItems));
    setTotalAmount(
      cartItems.reduce(
        (acc, curr) => acc + Number(curr.price) * curr.quantity,
        0
      )
    );
  }, [cartItems]);
  /**
   * 
   * Redux Toolkit, mt th vin qun l trng thi ph bin cho ng dng React.

    useSelector c s dng  truy xut trng thi t Redux store trong cc component React. N cho php bn chn v ly cc gi tr t store m bn quan tm v s dng chng trong component ca mnh.
   */
  const handlePayment = async () => {
    try {
      const response = await fetch("http://localhost:5000/create-payment-url", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
      });

      if (!response.ok) {
        throw new Error("Error creating payment URL");
      }

      const responseData = await response.json();
      setPaymentUrl(responseData.paymentUrl);

      // Redirect the user to the payment URL
      window.location.href = responseData.paymentUrl;
    } catch (error) {
      console.error("Error creating payment URL:", error);
    }
  };
  return (
    <Helmet title="Cart">
      {/* <CommonSection title="Shopping Cart" /> */}
      <section className="cart__wrapper" style={{ top: "10px" }}>
        <div className="cart__detail">
          <h4 className="cart__title">Gi hng ca bn</h4>

          {cartItems.length === 0 ? (
            <span className="cart__subtitle">Gi hng ca bn ang trng</span>
          ) : (
            <>
              <span className="cart__subtitle">
                Bn ang c <strong>{totalQuantity} sn phm</strong> trong gi
                hng
              </span>
              <table className="table bordered">
                <thead>
                  <tr>
                    <th>nh </th>
                    <th>Tn sn phm</th>
                    <th>Gi</th>
                    <th>S lng</th>
                    <th>Xa</th>
                  </tr>
                </thead>
                <tbody>
                  {cartItems.map((item, index) => (
                    <Tr item={item} key={index}></Tr>
                  ))}
                </tbody>
              </table>
            </>
          )}
        </div>

        <div lg="3" className="oder__summary">
          <div>
            <h4 className="cart__title">Thng tin n hng</h4>
            <div className="summary__total">
              <span
                className="d-flex align-items-center"
                style={{ fontSize: "18px" }}
              >
                Tng tin:
              </span>
              <span className="cart__total">
                {formatNumberWithCommas(totalAmount ?? 0)}
                <small
                  style={{
                    textDecoration: "underline",
                  }}
                >
                  
                </small>
              </span>
            </div>
          </div>
          <p>Ph vn chuyn s c tnh  trang thanh ton.</p>
          <p>Bn cng c th nhp m gim gi  trang thanh ton.</p>

          <div className="cart__btn">
            <div>
              <button className="buy__btn lg-7">
                <Link to="/shop">Tip tc mua sm</Link>
              </button>
            </div>

            <div
              style={{
                marginLeft: "20px",
              }}
            >
              <button
                className="buy__btn"
                style={{
                  backgroundColor: "#333",
                }}
                onClick={handlePayment}
              >
                <Link to="/checkout">Thanh ton</Link>
              </button>
            </div>
          </div>
        </div>
      </section>
    </Helmet>
  );
}
const Tr = ({ item }) => {
  const dispatch = useDispatch();
  const deleteProduct = () => {
    dispatch(cartActives.deleteItem(item.id));
  };
  return (
    <tr>
      <td>
        <img src={item.imgUrl} />
      </td>
      <td>{item.productName}</td>
      <td>
        {item.price}{" "}
        <small
          style={{
            textDecoration: "underline",
          }}
        >
          
        </small>
      </td>
      <td>{item.quantity} px</td>
      <td onClick={deleteProduct}>
        <i class="uil uil-trash"></i>
      </td>
    </tr>
  );
};

export default Cart;
